* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
section {
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}
article {
  position: relative;
  width: 50%;
  height: 40%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
article p {
  text-align: center;
  font-size: 1.3rem;
  text-wrap: balance;
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
    "Lucida Sans", Arial, sans-serif;
}

article p:first-of-type {
  margin: 20px;
}

article div button {
  position: absolute;
  left: 50px;
  bottom: 5px;
  width: 150px;
  height: 40px;
  font-size: 1rem;
  border-radius: 5px;
}
article div a {
  position: absolute;
  left: 250px;
  bottom: 15px;
}
